<script>
import dropDown from './dropDown.vue';
export default {
    name: 'focusTime',
    props: {
        closeBtn: Number,
        autoResume: Number,
        darkMode : Boolean
    },
    components: {
        dropDown,
    },
    data() {
        return {
            focusTime: localStorage.getItem('focusTime'),
            focusTimeValue: localStorage.getItem('focusTime'),
            seconds: 0,
            showSeconds: 0,
            intervalId: null,
            isIconChanged: true,
            showDropdown: true,
            fontBold:false,
        }
    },
    created() {
        this.showSeconds = this.padZero(this.showSeconds)
        document.addEventListener('click', this.handleDocumentClick);
        if (localStorage.getItem('focusTime') === null) {
            this.focusTime = 25
        }
        this.seconds = this.focusTime * 60
    },
    watch: {
        autoResume(newValue) {
            if (newValue === 1) {
                this.runOrpauseTimer();
            }
        }
    },
    methods: {
        runOrpauseTimer() {
            this.fontBold = true
            if (this.isIconChanged) {
                this.intervalId = setInterval(this.decreasTime, 1000)
                this.isIconChanged = false

            } else {
                this.isIconChanged = true
                clearInterval(this.intervalId)
            }
        },
        decreasTime() {
            if (this.seconds == 0) {
                clearInterval(this.intervalId)
                this.$emit('nextItem', 2)
                this.focusTime = localStorage.getItem('focusTime')
                this.isIconChanged = true
                this.fontBold =false
            } else {
                this.seconds--;
                this.focusTime = this.padZero(Math.floor(this.seconds / 60))
                this.showSeconds = this.padZero(this.seconds % 60)
            }

        },
        toggleDropdown() {
            this.showDropdown = !this.showDropdown
        },
        handleDocumentClick(event) {
            if (this.$el.contains(event.target)) {
                return;
            } else {
                this.showDropdown = true;
            }
            if (localStorage.getItem('focusTime') !== this.focusTimeValue) {
                this.focusTimeValue = localStorage.getItem('focusTime')
                this.focusTime = localStorage.getItem('focusTime')
                this.seconds = this.focusTime * 60
                this.showSeconds = this.padZero(0)
            }
        },
        showModal(color) {
            this.$emit('showModal', color)
        },
        nextItem() {
            this.$emit('nextItem', 2)
            clearInterval(this.intervalId)
            this.focusTime = localStorage.getItem('focusTime')
            this.seconds = this.focusTime * 60
            this.showSeconds = this.padZero(0)
            this.isIconChanged = true
            this.fontBold=false
        },
    },
    computed: {
        padZero() {
            return number => number.toString().padStart(2, '0');
        },
    },

}


</script>

